import React, { Component } from "react";
import Link from "gatsby-link";
import PowersList from '../../components/PowersList';
import SideNav from "../../components/SideNav/SideNav";
import { PageHeader } from "react-bootstrap";

class DocsPowers extends Component {
    constructor(props) {
        super(props);
        const data = this.props.data;
        this.state = {
            initialData: {...data},
            data: {}
        };
    }

    componentWillMount() {
        this.setState({data: {...this.state.initialData}});
    }

    handleFilterList(event) {
        let updatedList = {...this.state.initialData};
        updatedList.allMarkdownRemark.edges = updatedList.allMarkdownRemark.edges.filter(item => {
            return item.node.frontmatter.title.toLowerCase().search(
                event.target.value.toLowerCase()) !== -1;
        });
        this.setState({data: updatedList});
    }

    handleSearchList(event) {
        let value = event.target.value.toLowerCase();
        $("#powers div").filter(function() {
            $(this).toggle($(this).text().toLowerCase().indexOf(value) > -1);
        })
    }

    render() {
        const location = this.props.location.pathname;
        return (
            <div>
                <SideNav location={location} />
                <div className="container">
                    <h1></h1>
                    <div className="col-lg-12">
                        <div className="filter-list">
                            <form>
                                <fieldset className="form-group">
                                    <input type="text" className="form-control form-control-lg" placeholder="Search..." onChange={(e) => this.handleSearchList(e)}/>
                                </fieldset>
                            </form>
                        </div>
                    </div>
                    <div id="powers">
                        <PowersList skills={this.state.data} />
                    </div>
                </div>    
            </div>
        );
    }
}



export default DocsPowers;
    

export const query = graphql `
query PowersList {
    allMarkdownRemark (
        sort: {fields: [frontmatter___level], order: ASC},
        filter: {fileAbsolutePath: {regex: "/(docs)/(powers)/.*\\.md$/"}}
    ) {
        edges {
            node {
                id
                html
                frontmatter {
                    title
                    syntax
                    group
                    level
                    min_spirit
                    offensive
                }
            }
        }
    }
}
`


